body {
  font-family: "Crimson Text", serif;
  font-size: 21px;
  font-weight: 400; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif; }

h1 {
  font-weight: 700; }

input {
  border: 1px solid #e0e0e0;
  padding: 2.5px 7.5px; }

.bc_grid {
  display: flex;
  justify-content: space-between; }

.bc_col {
  flex: 1 1; }

[class*=bc_grid-cols] .bc_col {
  flex: none; }

.bc_grid-cols-2 .bc_col {
  width: 49%; }

.bc_grid-cols-3 .bc_col {
  width: 32%; }

/* TODO: Convert this to a variable */
@media (max-width: 576px) {
  .bc_grid {
    display: block; }
  .bc_col {
    margin-bottom: 15px; }
  .bc_grid-cols-2 .bc_col,
  .bc_grid-cols-3 .bc_col {
    width: 100%; } }

.bc_section {
  margin: 15px auto; }

* {
  box-sizing: border-box; }

body {
  margin: auto;
  max-width: 1400px;
  width: 100%; }

/* This needs to be removed in favor of containers */
a {
  color: #76C627;
  transition: all ease-in-out 0.3s; }
  a:hover {
    color: #69b123; }

.dg_site-header {
  position: relative; }

.dg_brand {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  position: absolute;
  text-align: center;
  text-transform: uppercase; }
  .dg_brand .dg_logo-container,
  .dg_brand .dg_logo {
    margin-bottom: 7.5px; }
  .dg_brand .dg_logo-container {
    background: #feb30c;
    font-size: 12px;
    font-weight: 900;
    padding: 7.5px 0 2.5px; }
  .dg_brand .dg_logo {
    max-width: 69px;
    width: 100%; }
  .dg_brand .dg_logo-subtext {
    font-size: 8px;
    letter-spacing: 6px; }

.dg_nav {
  margin: 0; }

.dg_primary-nav {
  background: #ededed;
  font-size: 16px;
  text-align: right; }
  .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item {
    padding: 0; }
    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item a {
      border-bottom: 3px solid transparent;
      border-top: 3px solid transparent;
      display: block;
      font-size: 14px;
      padding: 12px 15px; }
    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item i {
      transition: all ease-in-out 0.3s; }
    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item:hover a {
