    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item:hover i {
      color: #feb30c; }

.dg_secondary-nav {
  background: #333;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 100;
  padding: 0 15px;
  text-align: right; }
  .dg_secondary-nav .dg_secondary-nav-list a {
    padding: 15px; }
    .dg_secondary-nav .dg_secondary-nav-list a:hover {
      background: #616161; }
  .dg_secondary-nav a {
    color: #fff; }

.dg_primary-action-container {
  margin-top: 60px; }
  .dg_primary-action-container .dg_primacy-action-heading {
    font-weight: 700;
    margin-left: 7.5px; }

.dg_homepage-search {
  position: relative;
  max-width: 80%; }
  .dg_homepage-search input {
    width: 100%; }
  .dg_homepage-search i {
    background: #feb30c;
