.dg_icon-btn {
  background: #feb30c;
  cursor: pointer;
  transition: all ease-in-out 0.3s; }
  .dg_icon-btn:hover {
    background: #f0a601; }

.dg_callout {
  border-top: 5px solid #76C627; }
  .dg_callout .bc_callout-title {
    font-weight: 700;
    font-size: 28px; }
  .dg_callout .bc_callout-subtitle {
    color: #616161;
    font-family: "Montserrat", sans-serif;
    font-size: 12px;
    font-weight: 700;
